'use client';

import { useState, useEffect, useRef } from 'react';
import { UsersIcon, ShoppingBagIcon, ChatBubbleLeftRightIcon } from '@heroicons/react/24/outline';
import Link from 'next/link';

type Stats = {
  totalEmployees: number;
  totalProducts: number;
  totalReviews: number;
};

const AdminDashboard = () => {
  const [stats, setStats] = useState<Stats | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleMouseMove = (e: MouseEvent) => {
      if (containerRef.current) {
        const rect = containerRef.current.getBoundingClientRect();
        const x = e.clientX - rect.left;
        const y = e.clientY - rect.top;
        containerRef.current.style.setProperty('--mouse-x', `${x}px`);
        containerRef.current.style.setProperty('--mouse-y', `${y}px`);
      }
    };
    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);

  useEffect(() => {
    const fetchStats = async () => {
      try {
        const response = await fetch('/api/dashboard/stats');
        if (!response.ok) {
          throw new Error('Failed to fetch dashboard stats');
        }
        const data = await response.json();
        setStats(data);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchStats();
  }, []);

  const statCards = [
    {
      name: 'Total Employees',
      value: stats?.totalEmployees,
      icon: UsersIcon,
      href: '/admin/employees',
      bgColor: 'bg-pink-100',
      iconColor: 'text-pink-600',
    },
    {
      name: 'Total Products',
      value: stats?.totalProducts,
      icon: ShoppingBagIcon,
      href: '/admin/products',
      bgColor: 'bg-orange-100',
      iconColor: 'text-orange-600',
    },
    {
      name: 'Total Reviews',
      value: stats?.totalReviews,
      icon: ChatBubbleLeftRightIcon,
      href: '/admin/reviews',
      bgColor: 'bg-fuchsia-100',
      iconColor: 'text-fuchsia-600',
    },
  ];

  if (loading) {
    return (
      <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3">
        {[...Array(3)].map((_, i) => (
            <div key={i} className="bg-gray-200 h-36 rounded-lg animate-pulse"></div>
        ))}
      </div>
    );
  }

  if (error) {
    return <div className="text-red-500">Error: {error}</div>;
  }

  return (
    <div
      ref={containerRef}
      className="relative p-8 rounded-2xl shadow-lg bg-gray-900 before:absolute before:inset-0 before:bg-[radial-gradient(400px_circle_at_var(--mouse-x)_var(--mouse-y),rgba(236,72,153,0.2),transparent_40%)] before:opacity-0 before:transition-opacity before:duration-500 hover:before:opacity-100"
    >
      <div className="relative z-10">
        <h1 className="text-4xl font-bold tracking-tight text-transparent bg-clip-text bg-gradient-to-r from-pink-500 to-orange-400 mb-8">Admin Dashboard</h1>

        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3">
            {statCards.map((card) => (
                <div key={card.name} className="bg-gray-800/50 p-6 rounded-2xl shadow-lg border border-gray-700 hover:bg-gray-700/50 transition-colors duration-300">
                    <div className="flex items-start justify-between">
                        <div className="flex-shrink-0">
                            <span className={`inline-flex items-center justify-center h-12 w-12 rounded-xl ${card.bgColor}`}>
                                <card.icon className={`h-7 w-7 ${card.iconColor}`} aria-hidden="true" />
                            </span>
                        </div>
                        <Link href={card.href} className="text-sm font-medium text-pink-400 hover:text-pink-300">
                           View all <span className="sr-only">{card.name}</span>
                        </Link>
                    </div>
                    <div className="mt-4">
                        <p className="text-sm font-medium text-gray-400">{card.name}</p>
                        <p className="text-3xl font-bold text-white">{card.value ?? 'N/A'}</p>
                    </div>
                </div>
            ))}
        </div>
        
        <div className="mt-12 bg-gray-800/50 p-8 rounded-2xl shadow-lg border border-gray-700">
            <h2 className="text-2xl font-bold text-white">Welcome, Admin!</h2>
            <p className="mt-2 text-gray-400">
                From this dashboard, you can manage all aspects of the Dunkin' Feedback platform. Use the navigation on the left to get started.
            </p>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard;
